<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-lg-3 col-md-4 left-content" v-if="userData">
        <div class="photo" :style="{'backgroundImage': `url(${userData.photoURL})`}"></div>
        <div class="name"><p>{{userData.displayName}}</p></div>
        <div class="bio">
          <p v-if="detail.talent">音樂專長：{{detail.talent}}</p>
          <p v-else>音樂專長：不公開</p>
          <p v-if="detail.style">曲風：{{detail.style}}</p>
          <p v-else>曲風：不公開</p>
        </div>
        <button class="btn btn-outline-light" data-toggle="modal" data-target="#detail-modal">詳細資訊</button>
        <div class="editors"
          :class="{'menuSticky': tempDom.menuSticky}">
          <router-link to="/user/creatMusic" class="links">
            <i class="fas fa-plus-circle"></i> <span>發佈</span>音樂
          </router-link>
          <router-link to="/user/managePosts" class="links">
            <i class="fas fa-cog"></i> <span>管理</span>貼文
          </router-link>
          <router-link to="/user/editAccount" class="links">
            <i class="fas fa-user-cog"></i> <span>編輯</span>帳號
          </router-link>
          <router-link to="/music/loveSong" class="links">
            <i class="fab fa-gratipay"></i> 收藏<span>歌曲</span>
          </router-link>
        </div>
      </div>
      <div class="col-lg-9 col-md-8 right-content">
        <router-view :userData="userData" v-if="userData" :detail="detail"></router-view>
      </div>
    </div>
    <!-- Modal -->
    <div class="modal fade" id="detail-modal" tabindex="-1" role="dialog" aria-labelledby="detailModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="detailModalLabel"><i class="fas fa-info-circle"></i> 詳細資訊</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row" v-if="userData">
              <div class="col-md-6">
                <p><i class="fas fa-user"></i>姓名：<b>{{userData.displayName}}</b></p>
              </div>
              <div class="col-md-6">
                <p><i class="fas fa-venus-mars"></i>性別：
                  <b v-if="detail.gender">{{detail.gender}}</b>
                  <b v-else>不公開</b>
                </p>
              </div>
              <div class="col-md-6">
                <p><i class="fas fa-map-marker-alt"></i> 現居：
                  <b v-if="detail.city">{{detail.city}}</b>
                  <b v-else>不公開</b>
                </p>
              </div>
              <div class="col-md-6">
                <p><i class="fas fa-seedling"></i> 生日：
                  <b v-if="detail.birth">{{detail.birth}}</b>
                  <b v-else>不公開</b>
                </p>
              </div>
              <div class="col-md-12">
                <p><i class="far fa-id-card"></i> 現在任職：
                  <b v-if="detail.job">{{detail.job}}</b>
                  <b v-else>不公開</b>
                </p>
              </div>
              <div class="col-md-12">
                <p><i class="fas fa-music"></i> 音樂專長：
                  <b v-if="detail.talent">{{detail.talent}}</b>
                  <b v-else>不公開</b>
                </p>
              </div>
              <div class="col-md-12">
                <p><i class="fas fa-headphones"></i> 曲風：
                  <b v-if="detail.style">{{detail.style}}</b>
                  <b v-else>不公開</b>
                </p>
              </div>
              <div class="col-md-12">
                <p><i class="fas fa-universal-access"></i> 自我介紹：
                  <pre class="pre" v-linkified v-if="detail.bio" v-html="detail.bio">
                  </pre>
                  <b v-else>不公開</b>
                </p>
              </div>
              <hr>
              <div class="col-md-12">
                <p class="bd-n"><i class="far fa-paper-plane"></i> 聯絡方式：
                  <pre class="pre" v-linkified v-if="detail.contact" v-html="detail.contact">
                  </pre>
                  <b v-else>不公開</b>
                </p>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-primary" id="filter-btn" data-dismiss="modal">關閉視窗</button>
          </div>
        </div>
      </div>
    </div>	
  </div>
</template>

<script>
import firebase from 'firebase';
import $ from 'jquery';

export default {
  data() {
    return {      
      userData: null,
      detail: {},
      tempDom: {
        menuSticky: false, 
      }
    }
  },
  methods: {    
    getUserData() {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/getData`;
      firebase.auth().onAuthStateChanged(function(user) {
        if (user) {
          vm.$http.post(url, {"url": `users/${user.uid}/basic/`}).then((response) => {
            vm.userData = response.data;
          }).then(() => {
            vm.getUserDetail(vm.userData)
          })
        } else {
          vm.userData = null;
        }
      });
    },
    getUserDetail(user) {
      const vm = this;
      const url = `${process.env.VUE_APP_API}/getData`;
      vm.$http.post(url, {"url": `users/${user.uid}/detail/`}).then((response) => {
        if(response.data) {
          vm.detail = response.data;
        }
      })
    }
  },
  created() {
    const vm = this;
    vm.getUserData();    
  },
}
</script>

<style lang="scss" scoped>
.right-content{
  @media (max-width: 576px){
    padding: 20px 30px;
  }
  > div {
    margin-bottom: 50px;
    @media (max-width: 768px){
      padding-bottom: 30px;
    }
  }
}
.left-content {
  background-color: #fd8622;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-image: url(../assets/img/bg/wave-bg.svg);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 270%;
  height: 100vh;
  .photo {
    background-position: center;
    background-size: cover;
    border-radius: 50%;
    width: 160px;
    height: 160px;
    border: 8px solid #ff9b34;
    box-shadow: 0 0 4px #fff;
    position: relative;
    overflow: hidden;
  }
  .name {
    height: 70px;
    width: 100%;
    background-image: url(../assets/img/bg/hr.svg);
    background-size: 100% 100%;
    margin-top: 5px;
    p {
    font-size: 20px;
    padding-top: 3px;
    padding-left: 16px;
    font-weight: bold;
    color: #fff;
    } 
  }
  .bio {
    margin: 15px 0;
    width: 85%;
    p {
    max-width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    color: #fff;
    display: block;
    font-size: 14px;
    font-weight: 400;
    margin: 0 0 2px;
    }
  }
  .btn{
    align-self: flex-end;
  }
  .editors{
    display: flex;
    flex-direction: column;
    margin-top: 50px;
    align-self: flex-end;
    .links{
      color: rgba(50, 50, 50, 0.8);
      transition: all 0.2s ease-in-out;
      display: flex;
      justify-content: space-between;
      width: 100%;
      align-items: center;
      svg{
        margin-right: 5px;
      }
      &:hover{
        text-decoration: none;
        color: #fff;
      }
    }
  }
  @media (max-width: 767px){
    height: auto;
    // padding: 30px 0 0 0;
    padding-top: 30px;
    padding-bottom: 70px;
    background-position: 55%;
    overflow: hidden;
    .photo{
      width: 120px;
      height: 120px;
      border: 5px solid #ff9b34;
    }
    .name {
      background-image: url(../assets/img/bg/hr-sm.svg);
      padding: 0 5%;
    }
    .bio{
      display: none;
    }
    .btn{
      margin-right: 8%;
    }
    .editors{
      flex-direction: row;
      margin-top: 15px;
      width: 100%;
      justify-content: space-between;
      background-color: rgba(255, 255, 255, 0.8);
      position: absolute;
      bottom: 0;
      left: 0;
      &.menuSticky{
        position: fixed;
        top: 50px;
      }
      .links{
        flex: 1;
        text-align: center;
        display: block;
        padding: 13px 0;
        position: relative;
        span{
          display: none;
        }
        &::before{
          content: '';
          height: calc(100% - 25px);
          width: 1px;
          background-color: rgba(#000, .3);
          position: absolute;
          left: 0;
        }
        &:first-child{
          border: none;
          &::before{
            content: none;
          }
        }
        &:hover{
          background-color: rgba(50, 50, 50, 0.8)
        }
      }
    }
  }
}
#detail-modal{
  .modal-title{
    font-weight: bold;
    svg{
      color: #999;
    }
  }
  .modal-body{
    .pre{
      margin-bottom: 5px;
    }
    p{
      color: #888;
      margin: 8px 0;
      border-bottom: 2px #eee solid;
      padding-bottom: 5px;
      svg{
        color: #fecc5b;
        font-weight: bold;
        width: 25px;
        font-size: 16px;
      }
      b{
        color: #222;
      }
      &.bd-n{
        border: none;
      }
    }
  }
}
</style>
